import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

export const fetchAccessToken = createAsyncThunk('token/fetch', async () => {
  const res = await fetch(`${process.env.REACT_APP_AUTH_URL}/oauth/token`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      Authorization: `Basic ${btoa(`${process.env.REACT_APP_CLIENT_ID}:${process.env.REACT_APP_CLIENT_SECRET}`)}`,
    },
    body: `grant_type=client_credentials&scope=manage_project:${process.env.REACT_APP_PROJECT_KEY}`,
  });

  return (await res.json())?.access_token as string;
});

interface IAuthState {
  token: string;
}

const initialState: IAuthState = {
  token: '',
};

export const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder.addCase(fetchAccessToken.fulfilled, (state, action) => {
      state.token = action.payload;
    });
  },
});

export default authSlice.reducer;
