import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { store } from '../index';

interface IUserState {
  firstName: string;
  lastName: string;
  email: string;
  isSignedIn: boolean;
}

interface ICustomer {
  firstName: string;
  lastName: string;
  password: string;
  email: string;
}

interface ICustomerResponse {
  customer: {
    addresses: [];
    email: string;
    firstName: string;
    id: string;
    isEmailVerified: boolean;
    lastName: string;
    password: string;
    version: number;
    createdAt: string;
    lastModifiedAt: string;
    authenticationMode: string;
  }
}

export const registerNewCustomer = createAsyncThunk('user/registration', async (customer: ICustomer) => {
  const res = await fetch(`${process.env.REACT_APP_API_URL}/${process.env.REACT_APP_PROJECT_KEY}/customers`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${store.getState().auth.token}`,
    },
    body: JSON.stringify(customer),
  });
  return (await res.json()) as ICustomerResponse;
});

export const login = createAsyncThunk('user/login', async ({ email, password }: { email: string, password: string }) => {
  const res = await fetch(`${process.env.REACT_APP_API_URL}/${process.env.REACT_APP_PROJECT_KEY}/login`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${store.getState().auth.token}`,
    },
    body: JSON.stringify({ email, password }),
  });
  return (await res.json()) as ICustomerResponse;
});

const initialState: IUserState = {
  firstName: '',
  lastName: '',
  email: '',
  isSignedIn: false,
};

export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    signOut: (state) => {
      state.firstName = '';
      state.lastName = '';
      state.email = '';
      state.isSignedIn = false;
    },
  },
  extraReducers(builder) {
    builder
      .addCase(registerNewCustomer.fulfilled, (state, action) => {
        state.firstName = action.payload.customer.firstName;
        state.lastName = action.payload.customer.lastName;
        state.email = action.payload.customer.email;
        state.isSignedIn = true;
      })
      .addCase(login.fulfilled, (state, action) => {
        state.firstName = action.payload.customer.firstName;
        state.lastName = action.payload.customer.lastName;
        state.email = action.payload.customer.email;
        state.isSignedIn = true;
      });
  },
});

export const { signOut } = userSlice.actions;

export default userSlice.reducer;
