import React, { type FC } from 'react';

import IconButton from '@mui/material/IconButton/IconButton';
import MenuItem from '@mui/material/MenuItem/MenuItem';
import Typography from '@mui/material/Typography/Typography';
import MenuIcon from '@mui/icons-material/Menu';
import Menu from '@mui/material/Menu/Menu';

import { routes } from '../../routes/routes';
import { IMenuProps } from '../../models/interfaces';

export const Burger:FC<IMenuProps> = ({
  open, close, currentElement,
}) => (
  <>
    <IconButton
      size="large"
      aria-label="account of current user"
      aria-controls="menu-appbar"
      aria-haspopup="true"
      onClick={open}
      color="inherit"
      style={{ padding: 0 }}
    >
      <MenuIcon />
    </IconButton>
    <Menu
      id="menu-appbar"
      anchorEl={currentElement}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      keepMounted
      transformOrigin={{
        vertical: 'top',
        horizontal: 'left',
      }}
      open={Boolean(currentElement)}
      onClose={close}
      sx={{
        display: { xs: 'block', md: 'none' },
      }}
    >
      {routes.map((route) => (
        <MenuItem key={route} onClick={close}>
          <Typography fontFamily="Montserrat Alternates" textAlign="center">{route}</Typography>
        </MenuItem>
      ))}
    </Menu>
  </>

);
