import React, { type FC, type ReactNode } from 'react';
import { NavLink } from 'react-router-dom';

import Avatar from '@mui/material/Avatar/Avatar';
import Box from '@mui/material/Box/Box';
import IconButton from '@mui/material/IconButton/IconButton';
import Menu from '@mui/material/Menu/Menu';
import MenuItem from '@mui/material/MenuItem/MenuItem';
import Tooltip from '@mui/material/Tooltip/Tooltip';
import AccountIcon from '@mui/icons-material/AccountCircle';

import s from './style.module.scss';

import { IMenuProps } from '../../models/interfaces';
import { guestMenu, userMenu } from '../../routes/routes';

interface IUserMenu extends IMenuProps {
  auth: boolean;
}

export const UserMenu: FC<IUserMenu> = ({
  open, close, currentElement, auth,
}) => {
  const currentMenu = (route: string): ReactNode => (
    <MenuItem key={route} onClick={close}>
      <NavLink
        className={({ isActive }) => (`${s.navLink} ${isActive && s.active}`)}
        to={`/${route.toLowerCase().replaceAll(' ', '')}`}
      >
        {route}
      </NavLink>
    </MenuItem>
  );

  return (
    <Box sx={{ flexShrink: 0 }}>
      <Tooltip title="Open settings">
        <IconButton onClick={open} sx={{ p: 0 }}>
          {
            auth
              ? <Avatar sx={{ width: 50, height: 50 }} alt="Test User" src="/avatar.png" />
              : <AccountIcon sx={{ width: 50, height: 50 }} />
          }
        </IconButton>
      </Tooltip>
      <Menu
        sx={{ mt: '45px' }}
        id="menu-appbar"
        anchorEl={currentElement}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        keepMounted
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={Boolean(currentElement)}
        onClose={close}
      >
        {auth ? userMenu.map(currentMenu) : guestMenu.map(currentMenu)}
      </Menu>
    </Box>
  );
};
