import React, { type FC } from 'react';

import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar/Toolbar';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box/Box';

import { Logo } from '../logo';
import { Burger } from '../burger';
import { NavBar } from '../nav-bar';
import { UserMenu } from '../user-menu';
import { useAppSelector } from '../../hooks/redux-typed-hooks';

export const Header:FC = () => {
  const isAuth = useAppSelector((state) => state.user.isSignedIn);
  const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(null);
  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(null);

  const handleOpenUserMenu = (event: React.MouseEvent<HTMLElement>):void => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseUserMenu = ():void => {
    setAnchorElUser(null);
  };

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>):void => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = ():void => {
    setAnchorElNav(null);
  };

  return (
    <AppBar position="static">
      <Container style={{ paddingBlock: '5px' }}>
        <Toolbar disableGutters>
          <Box sx={{
            display: 'flex',
            justifyContent: 'center',
            mr: { md: 10 },
            flexGrow: { xs: 1, md: 0 },
          }}
          >
            <Logo />
          </Box>

          <Box sx={{ order: { xs: -1 }, display: { xs: 'flex', md: 'none' } }}>
            <Burger
              close={handleCloseNavMenu}
              open={handleOpenNavMenu}
              currentElement={anchorElNav}
            />
          </Box>

          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            <NavBar />
          </Box>

          <UserMenu
            currentElement={anchorElUser}
            open={handleOpenUserMenu}
            close={handleCloseUserMenu}
            auth={isAuth}
          />
        </Toolbar>
      </Container>
    </AppBar>
  );
};
